<template>
  <div>
    <el-form
      class=""
      style="padding: 0 60px 0 20px"
      ref="dialogFormRef"
      :model="dialogForm"
      :rules="dialogFormRules"
      label-width="100px"
    >
      <el-row>
        <div>
          <p class="dialog-title">基本信息</p>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="表名称" prop="table_name">
                <el-input v-model="dialogForm.table_name"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="备注" prop="description">
            <el-input
              type="textarea"
              v-model="dialogForm.description"
            ></el-input>
          </el-form-item>
        </div>
        <div>
          <p class="dialog-title">字段明细</p>
          <el-button type="primary" size="small" plain @click="AddListRow()"
            >添加字段</el-button
          >
          <el-table
            :data="dialogForm.response_datas"
            style="width: 100%"
            class="dialogTable"
            height="350"
          >
            <el-table-column fixed="left" label="字段名称" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.field_name'"
                  :rules="[{ required: true, message: '字段名称不能为空' }]"
                >
                  <el-input v-model="scope.row.field_name"></el-input>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="字段类型" align="center" >
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.field_type'"
                  :rules="[{ required: true, message: '类型不能为空' }]"
                >
                  <el-select
                    v-model="scope.row.field_type"
                    placeholder="请选择"
                  >
                    <el-option label="INT" value="INT"></el-option>
                    <el-option label="VARCHAR" value="VARCHAR"></el-option>
                    <el-option label="DATETIME" value="DATETIME"></el-option>
                    <el-option label="FLOAT" value="FLOAT"></el-option>
                  </el-select>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="主键" align="center">
              <template slot-scope="scope">
                <!-- class="textRadio" -->

                <el-form-item>
                  <el-radio
                   :label="scope.$index"
                    v-model="radio"
                    class="textRadio"
                    >{{&nbsp;}}</el-radio
                  >
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="不可为空" align="center">
              <template slot-scope="scope">
                <!-- class="textRadio" -->
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.field_NN'"
                  :rules="[{ required: true, message: '类型不能为空' }]"
                >
                  <el-checkbox v-model="scope.row.field_NN"></el-checkbox>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="自增" align="center">
              <template slot-scope="scope">
                <!-- class="textRadio" -->
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.field_AI'"
                  :rules="[{ required: true, message: '类型不能为空' }]"
                >
                  <el-checkbox v-model="scope.row.field_AI"></el-checkbox>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="操作" width="80" align="center">
              <template slot-scope="scope">
                <el-form-item>
                  <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(scope.$index, scope.row)"
                    >删除</el-button
                  >
                </el-form-item>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-row>
    </el-form>

    <div slot="footer" class="dialog-footer mt20" style="text-align: right">
      <el-button @click="dialogFlag = false">取 消</el-button>
      <el-button type="primary" @click="colDeleteBtn">删 除</el-button>
      <el-button type="primary" @click="dialogSave">确 定</el-button>
    </div>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState(["CurrentUserInfo"]),
  },
  props:{
      work_code:{
          type:String,
          default:""
      }
  },
  data() {
    return {
      radio: 0,
      dialogForm: {
        work_code: "",
        table_name: "",
        description: "",
        response_datas: [
          {
            field_name: "",
            field_type: "",
            field_PK: false,
            field_NN: false,
            field_AI: false,
          },
        ],
      },
      dialogFormBase: {
        work_code: "",
        table_name: "",
        description: "",
        response_datas: [
          {
            field_name: "",
            field_type: "",
            field_PK: false,
            field_NN: false,
            field_AI: false,
          },
        ],
      },
      dialogFormRules: {
        product_name: [
          { required: true, message: "请输入产品名称", trigger: "blur" },
        ],
        product_id: [
          { required: true, message: "请输入产品编码", trigger: "blur" },
          { min: 3, max: 18, message: "长度为3~18个字节!", trigger: "blur" },
          {
            trigger: "blur",
            validator: (rule, value, callback) => {
              var productId = /^[a-z0-9]+$/;
              console.log(productId.test(value));
              if (!productId.test(value)) {
                callback(new Error("仅允许小写字母和数字!"));
              } else {
                callback();
              }
            },
          },
        ],
      },
    };
  },
  methods: {
    //点击加号添加一行数据
    AddListRow() {
      this.dialogForm.response_datas.push({
        field_name: "",
        field_type: "",
        field_PK: false,
        field_NN: false,
        field_AI: false,
      });
    },

    // 点击删除当前行
    handleDelete(index, rowData) {
      if (rowData.field_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            prodect_code: rowData.prodect_code,
            response_datas: JSON.stringify([
              { field_code: rowData.field_code },
            ]),
          };
          this.getLogisticDeleteBOMProductList(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },

    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          table_code: rowData.table_code,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteBOMProductList(params);
      });
    },
    // 弹出层确定
    dialogSave() {
      console.log(this.radio);

      this.dialogForm.work_code = this.work_code;
      this.dialogForm.response_datas[this.radio].field_PK = true;
      console.log(this.dialogForm);

      /*  this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增产品") {
            console.log("新增产品", this.dialogForm);
            this.postLogisticBOMProductList(this.dialogForm);
          } else {
            console.log("修改产品", this.dialogForm);
            this.dialogForm.response_datas = JSON.stringify(
              this.dialogForm.response_datas
            );
            console.log(
              "this.dialogForm.response_datas",
              this.dialogForm.response_datas
            );

            this.getLogisticPutBOMProductList(this.dialogForm);
          }
        }
      }); */
    },
  },
};
</script>


<style lang="scss" scoped>
.dialogTable {
  .el-form-item {
    // margin: 0;
    /deep/ .el-form-item__content {
      margin: 0 !important;
    }
  }
}
</style>