<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="工站编码">
          <el-input v-model="listQuery.work_id"></el-input>
        </el-form-item>
        <el-form-item label="工站名称">
          <el-input v-model="listQuery.work_name"></el-input>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>工站列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addUser"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增工站 弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="工站编码" prop="work_id">
          <el-input
            v-model="dialogForm.work_id"
            v-if="!workIdReadonly"
          ></el-input>
          <span v-else>{{dialogForm.work_id}}</span>
        </el-form-item>
        <el-form-item label="工站名称" prop="work_name">
          <el-input v-model="dialogForm.work_name"></el-input>
        </el-form-item>
        <el-form-item label="工站类型" prop="work_type">
          <el-select
            v-model="dialogForm.work_type"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="加工" value="加工"></el-option>
            <el-option label="测试" value="测试"></el-option>
            <el-option label="维修" value="维修"></el-option>
            <el-option label="包装" value="包装"></el-option>
            <el-option label="检验" value="检验"></el-option>
            <el-option label="入库出库" value="入库出库"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="上级工站" prop="leader_work_id">
          <el-select
            v-model="dialogForm.leader_work_id"
            placeholder="请选择"
            style="width: 100%"
            @change="leader_work_id_change"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialogForm'"
              :label="item.work_id+'_'+item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
      title="表管理"
      width="1000px"
      :visible.sync="dialogEditTableFlag"
      :close-on-click-modal="false"
    >
      <WorkStationListEditTable
        :work_code="work_code"
      ></WorkStationListEditTable>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import WorkStationListEditTable from "@/views/manager/baseManagement/WorkStationListEditTable";
export default {
  name: "WorkstationList",
  components: {
    ELTable,
    WorkStationListEditTable,
  },
  computed: {
    ...mapState(["WorkCodeNameList"]),
  },
  data() {
    return {
      // 筛选
      work_code: "",
      workIdReadonly: false,
      listQuery: {
        work_id: "",
        work_name: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        work_id: "",
        work_name: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增工站弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogEditTableFlag: false,
      dialogForm: {
        work_id: "",
        work_name: "",
        work_type: "",
        leader_work_id: "",
      },
      dialogFormBase: {
        work_id: "",
        work_name: "",
        work_type: "",
        leader_work_id: "",
      },
      dialogFormRules: {
        work_id: [
          { required: true, message: "请输入工站编码", trigger: "blur" },
          { min: 3, max: 18, message: "长度为3~18个字节!", trigger: "blur" },
          {
            trigger: "blur",
            validator: (rule, value, callback) => {
              var productId = /^[a-zA-Z0-9_]+$/;
              console.log(productId.test(value));
              if (!productId.test(value)) {
                callback(new Error("仅允许大小写字母和下划线!"));
              } else {
                callback();
              }
            },
          },
        ],
        work_name: [
          { required: true, message: "请输入工站名称", trigger: "blur" },
        ],
        work_type: [
          { required: true, message: "请选择工站类型", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "工站编码",
          field: "work_id",
          align: "center",
        },
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "工站类型",
          field: "work_type",
          align: "center",
        },
        {
          label: "上级工站",
          field: "leader_work_name",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
            // {
            //   label: "管理表",
            //   event: this.colEditTable,
            // },
          ],
        },
      ],
      tableData: [],
    };
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticWorkStation(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticWorkStation(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticWorkStation(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticWorkStation(this.listQuery);
    },
    // 工站列表 查询
    getLogisticWorkStation(params) {
      baseManagement.getLogisticWorkStation(params).then((res) => {
        console.log("工站列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.leader_work_id == CodeNameListItem.work_id) {
                  tableDataItem.leader_work_name = CodeNameListItem.work_id+'_'+CodeNameListItem.work_name;

              }
            });
            
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 工站列表 新增
    postLogisticWorkStation(params) {
      baseManagement.postLogisticWorkStation(params).then((res) => {
        console.log("工站列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticWorkStation(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 工站列表 新增
    addUser() {
      this.dialogTitle = "新增工站";
      this.dialogFlag = true;
      this.workIdReadonly = false;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
      
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    },

    // 工站列表 修改
    colEditBtn(rowData) {
    this.getLogisticWorkCodeName();

      console.log(rowData);
      this.dialogTitle = "修改工站";
      this.dialogFlag = true;
      this.workIdReadonly = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        work_code: rowData.work_code,
        work_id: rowData.work_id,
        work_name: rowData.work_name,
        work_type: rowData.work_type,
        leader_work_id: rowData.leader_work_id,
      };
    },

    // 工站列表 修改
    getLogisticPutWorkStation(params) {
      baseManagement.getLogisticPutWorkStation(params).then((res) => {
        console.log("工站列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticWorkStation(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    leader_work_id_change() {
      console.log(this.dialogForm.work_id);

      console.log(this.dialogForm.leader_work_id);
    },

    // 弹出层确认按钮
    dialogSave() {
      console.log(this.dialogTitle);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          if (this.dialogForm.work_id == this.dialogForm.leader_work_id) {
            this.$message({
              message: "本工站和上级工站不可相同!",
              type: "error",
            });
            return false
          }
          //验证通过调用保存接口
          if (this.dialogTitle == "新增工站") {
            console.log("新增工站", this.dialogForm);
            this.postLogisticWorkStation(this.dialogForm);
          } else if (this.dialogTitle == "修改工站") {
            console.log("修改工站", this.dialogForm);
            this.getLogisticPutWorkStation(this.dialogForm);
          }
        }
      });
    },

    // 工站列表 删除
    getLogisticDeleteWorkStation(params) {
      baseManagement.getLogisticDeleteWorkStation(params).then((res) => {
        console.log("工站列表 删除", res);
        if (res.code == 0) {
          this.getLogisticWorkStation(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteWorkStation({ work_code: rowData.work_code });
      });
    },

    colEditTable(rowData) {
      this.work_code = rowData.work_code;
      this.dialogEditTableFlag = true;
    },
  },
  mounted() {
    // 工站列表 查询
    this.getLogisticWorkStation(this.listQuery);
  },
};
</script>

